<script>
	import Icon from './Icon.svelte';
	import { ascending, range } from 'd3-array';
	export let data;
	export let columns;
	export let paginate;

	$: sort = columns[0];
	$: flipChevron = 0;
	$: sortedData = sortData(columns[0]);
	$: pages = range(data.length / paginate);
	$: activePage = 1;

	const onClick = (column) => {
		flipChevron = sort === column ? flipChevron + 1 : 0;
		sort = column;
		sortedData = sortData(column);
	};
	const isOdd = (num) => {
		return num % 2;
	};
	const sortData = (column) => {
		let tableData;
		var dataType = typeof data[0][sort];

		if (dataType === 'number') {
			tableData = data.sort((a, b) => ascending(+a[column], +b[column]));
		} else if (dataType === 'string') {
			tableData = data.sort((a, b) =>
				ascending(
					a[column].split(' ')[a[column].split(' ').length - 1],
					b[column].split(' ')[b[column].split(' ').length - 1]
				)
			);
		} else {
			tableData = data.sort((a, b) => ascending(a[column], b[column]));
		}

		return flipChevron % 2 ? tableData.reverse() : tableData;
	};
	const pagination = (direction) => {
		if (direction === 'prev' && activePage !== 1) {
			activePage = activePage - 1;
		} else if (direction === 'next' && activePage !== pages.length) {
			activePage = activePage + 1;
		} else if (typeof direction === 'number') {
			activePage = direction;
		}
	};
</script>

<table>
	<thead>
		<tr>
			<!-- iterate through chosen columns from prop -->
			{#each columns as column}
				<th
					on:click={() => onClick(column)}
					class:activeSortColumn={sort === column}
					>{column}
					<span class="icon"
						><Icon
							name="chevron-down"
							direction={isOdd(flipChevron) && sort === column ? 's' : 'n'}
						/></span
					>
				</th>
			{/each}
		</tr>
	</thead>
	<tbody>
		<!-- data from csv read in -i-->
		{#each [...sortedData].slice((activePage - 1) * +paginate, (activePage - 1) * +paginate + +paginate) as d}
			<tr>
				<!-- columns specified as prop -->
				{#each columns as column}
					<td>{d[column]}</td>
				{/each}
			</tr>
		{/each}
	</tbody>
</table>
{#if paginate}
	<div id="pagination">
		<button
			class:inactive={activePage === 1}
			on:click={() => pagination('prev')}><Icon name="chevron-left" /></button
		>
		{#each pages as page}
			<button
				class:active={page + 1 === activePage}
				on:click={() => pagination(page + 1)}>{page + 1}</button
			>
		{/each}
		<button
			class:inactive={activePage === pages.length}
			on:click={() => pagination('next')}><Icon name="chevron-right" /></button
		>
	</div>
{/if}

<style lang="scss">.hide {
  display: none; }

#pagination button {
  padding: 0.25em;
  margin: 0.05em;
  height: 100%;
  vertical-align: middle;
  border: 1px solid transparent;
  transition: all 0.2s ease-in-out; }
  #pagination button:hover {
    background-color: var(--gray2); }
  #pagination button.active {
    color: var(--blue3);
    border: 1px solid var(--blue3); }
  #pagination button.inactive {
    opacity: 0.4; }

table {
  overflow-x: scroll;
  padding: 1em; }
  table thead th {
    padding: 0.25em;
    cursor: pointer;
    text-transform: uppercase;
    border-bottom: 2px solid black;
    user-select: none; }
    table thead th.activeSortColumn {
      border-color: var(--blue3);
      color: var(--blue3); }
  table tr:nth-of-type(2n) {
    background-color: var(--gray2); }
</style>
